import React from 'react'
import { Featured } from '../../../App/ReactSelect/SelectData'
import { ReactSelectListing } from '../ReactSelectListing.component'
export const HalfMapSearchResult=({more})=> {
    return (
        <div className="grid_list_search_result mt30-991">
            <div className="col-sm-12 col-md-4 col-lg-4 col-xl-5">
                <div className="left_area tac-xsd">
                    <p>9 Search results</p>
                </div>
            </div>
            <div className="col-sm-12 col-md-8 col-lg-8 col-xl-7">
            <div className="right_area style2 text-right grid-select-container hfmp">
                <ul >
                    <li className="list-inline-item">
                    <span className="shrtby">Sort by:&nbsp;</span>
                    <ReactSelectListing data={Featured}/>
                    </li>
                    <li className="list-inline-item"><a className="hvr-text-thm" href="#"><span className="fa fa-th-large"></span></a></li>
                    <li className="list-inline-item"><a className="hvr-text-thm" href="#"><span className="fa fa-th-list"></span></a></li>
                    {more ? <li className="list-inline-item"><a className="hvr-text-thm" href="#"><span className="flaticon-more"></span></a></li> : null}
                </ul>
			</div>
              
            </div>
        </div>
    )
}
