import React,{useState} from 'react'
import { useStaticQuery, graphql, Link } from 'gatsby'
import {Row , Col, Dropdown} from 'react-bootstrap'
import {HalfMapSearchResult} from '../../../../Global/Pages/Listing/HalfMap/HalfMapSearchResult.component'
import GoogleMapReact from 'google-map-react'
import PropertyItem from '../../../../Global/Pages/PropertyList.component'
import Pagination from '../../../../Global/Pages/Pagination.component'
import AdvanceSearchWidgetSidebar from '../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
import ReactSelect from '../../../../Global/App/ReactSelect/ReactSelect'
import { Buy,bathrooms,bedrooms,yearbuilt,builtuparea } from '../../../../Global/App/ReactSelect/SelectData'
const AdvanceSearchHalfMap=()=>{
    return(
        <div className="home1-advnc-search home2 style2 mt30 mb30 dn-991">
        <ul className="h1ads_1st_list half_style mb0">
            <li className="custome_fields_half list-inline-item">
                <div className="search_option_two home2">
                    <div className="candidate_revew_select">
                        <ReactSelect data={Buy}/>
                    </div>
                </div>
            </li>
            <li className="custome_fields_half list-inline-item">
                <div className="form-group">
                    <input type="text" className="form-control" placeholder="Search by City, Neighborhood, or Address"/>
                </div>
            </li>
            <li className="custome_fields_half custome_fields_520 list-inline-item">
                <div className="navbered">
                    <div className="mega-dropdown home2">
                        <Dropdown>
                            <Dropdown.Toggle variant="custom" className="dropbtn">
                                <span id="show_advbtn" className="dropbtn">
                                    Advanced &nbsp; <i className="flaticon-more pl10 flr-520"></i>
                                </span>
                            </Dropdown.Toggle>

                            <Dropdown.Menu className="dropdown-content">
                                <Row className="p15">
                                    <Col lg={12}><h4 className="text-thm3">Amenities</h4></Col>
                                    <Col>
                                        <ul className="ui_kit_checkbox selectable-list">
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck1" />
                                                    <label className="custom-control-label" for="customCheck1">Air Conditioning</label>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck2" />
                                                    <label className="custom-control-label" for="customCheck2">Lawn</label>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck3" />
                                                    <label className="custom-control-label" for="customCheck3">Swimming Pool</label>
                                                </div>
                                            </li>
                                        </ul>
                                    </Col>
                                    <Col>
                                        <ul className="ui_kit_checkbox selectable-list">
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck4" />
                                                    <label className="custom-control-label" for="customCheck4">Barbeque</label>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck5" />
                                                    <label className="custom-control-label" for="customCheck5">Microwave</label>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck6" />
                                                    <label className="custom-control-label" for="customCheck6">TV Cable</label>
                                                </div>
                                            </li>
                                        </ul>
                                    </Col>
                                    <Col>
                                        <ul className="ui_kit_checkbox selectable-list">
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck7" />
                                                    <label className="custom-control-label" for="customCheck7">Dryer</label>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck8" />
                                                    <label className="custom-control-label" for="customCheck8">Outdoor Shower</label>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck9" />
                                                    <label className="custom-control-label" for="customCheck9">Washer</label>
                                                </div>
                                            </li>
                                        </ul>
                                    </Col>
                                    <Col>
                                        <ul className="ui_kit_checkbox selectable-list">
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck10" />
                                                    <label className="custom-control-label" for="customCheck10">Gym</label>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck11" />
                                                    <label className="custom-control-label" for="customCheck11">Refrigerator</label>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck12" />
                                                    <label className="custom-control-label" for="customCheck12">WiFi</label>
                                                </div>
                                            </li>
                                        </ul>
                                    </Col>
                                    <Col>
                                        <ul className="ui_kit_checkbox selectable-list">
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck13" />
                                                    <label className="custom-control-label" for="customCheck13">Laundry</label>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck14" />
                                                    <label className="custom-control-label" for="customCheck14">Sauna</label>
                                                </div>
                                            </li>
                                            <li>
                                                <div className="custom-control custom-checkbox">
                                                    <input type="checkbox" className="custom-control-input" id="customCheck15" />
                                                    <label className="custom-control-label" for="customCheck15">Window Coverings</label>
                                                </div>
                                            </li>
                                        </ul>
                                    </Col>
                                </Row>
                                <Row className="p15 pt0-xsd">
                            <Col xl="10" lg="11">
                                <ul className="apeartment_area_list mb0">
                                    <li className="list-inline-item">
                                        <div className="candidate_revew_select" style={{zIndex:'2222'}}>
                                        <ReactSelect data={bathrooms} tickColor='#ff5a5f' />
                                        </div>
                                    </li>
                                    <li className="list-inline-item">
                                        <div className="candidate_revew_select">
                                            <ReactSelect data={bedrooms} tickColor='#ff5a5f' />

                                        </div>
                                    </li>
                                    <li className="list-inline-item">
                                        <div className="candidate_revew_select">
                                           <ReactSelect data={yearbuilt} tickColor='#ff5a5f' />

                                        </div>
                                    </li>
                                    <li className="list-inline-item">
                                        <div className="candidate_revew_select">
                                            <ReactSelect data={builtuparea} tickColor='#ff5a5f' />
                                        </div>
                                    </li>
                                </ul>
                            </Col>
                            <Col xl="2" lg="1"></Col>
                        </Row>
                    
                            </Dropdown.Menu>
                        </Dropdown>

                        </div>
                </div>
            </li>
        </ul>
    </div>
    )
}

const HalfMapV1Section=()=> {
    const [SideBarShow, setSideBarShow] = useState(false);
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
        if(SideBarShow){
            document.querySelector('body').classList.add('body_overlay')
            document.querySelector('.sidebar_content_details').classList.add('sidebar_ml0')
        }else{
            document.querySelector('body').classList.remove('body_overlay')
            document.querySelector('.sidebar_content_details').classList.remove('sidebar_ml0')
        }
    }
    const data = useStaticQuery(graphql`
        query{
            fp1: file(relativePath: {eq: "property/fp1.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp2: file(relativePath: {eq: "property/fp2.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp3: file(relativePath: {eq: "property/fp3.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp4: file(relativePath: {eq: "property/fp4.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp5: file(relativePath: {eq: "property/fp5.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp15: file(relativePath: {eq: "property/fp15.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp16: file(relativePath: {eq: "property/fp16.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp17: file(relativePath: {eq: "property/fp17.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp18: file(relativePath: {eq: "property/fp18.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp19: file(relativePath: {eq: "property/fp19.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp20: file(relativePath: {eq: "property/fp20.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
        }`)
    return (
       <section id="feature-property" className="our-listing bgc-f7 pt0 pb0">
		<div className="container-fluid">
			<div className="row">
                    <div className="col-lg-12">
                        <div className="listing_sidebar dn db-991">
                            <div className="sidebar_content_details style3">
                                <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                    <div className="sidebar_advanced_search_widget">
                                        <h4 className="mb25">
                                        Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                        </Link>
                                        </h4>
                                        <AdvanceSearchWidgetSidebar style2/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
			<div className="row">
				<div className="col-lg-12">
					<div className="sidebar_switch mobile_style dn db-991 mt30 mb0">
						<div id="main2">
							<span id="open2" className="flaticon-filter-results-button filter_open_btn style4" onClick={handleSidebar}> Show Filter</span>
						</div>
					</div>
				</div>
				<div className="col-xl-4">
					<div className="half_map_area">
						<div className="home_two_map style2">
                        <div className="map-canvas skin2 half_style" style={{position: "relative", overflow: "hidden"}}>
                           <GoogleMapReact
						bootstrapURLKeys={{ key: 'AIzaSyAAz77U5XQuEME6TpftaMdX0bBelQxXRlM' }}
						defaultCenter={{
										lat: 51.510280,
										lng: -0.084028
										}}
						defaultZoom={14}
						>
							<div
								>
									Awesome Place
							</div>
        			</GoogleMapReact>
                    </div>
						</div>
					</div>
				</div>
				<div className="col-xl-8">
					<div className="half_map_area_content">
	 					<div className="row">
							<div className="col-lg-12">
								<AdvanceSearchHalfMap/>
							</div>
						</div>
						<div className="row">
                            <HalfMapSearchResult />
						</div>
						<div className="row">
                            <div className="col-lg-12">
                            <ul className="feature_property_half_clist mb0">
							<li className="extrawide list-inline-item">
                                <PropertyItem listfor='For Rent'
                                featured
                                price="13000"
                                category="Appartment"
                                title="Renovated Apartment"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg}alt="fp2.jpg"/>
                                    </div>
                                </PropertyItem>
                            </li>
                            <li className="extrawide list-inline-item">
                            <PropertyItem listfor='For Rent'
                                price="13000"category="Villa"
                                title="Gorgeous Villa Bay View"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg}alt="fp2.jpg"/>
                                    </div>
                                </PropertyItem>
                            </li>
                            <li className="extrawide list-inline-item">
                                <PropertyItem listfor='For Sale'
                                    price="13000"category="Single Family Home"
                                    title="Luxury Family Home"
                                    >
                                        <div className="item">
                                            <img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp3.jpg"/>
                                        </div>
                                        <div className="item">
                                            <img className="img-whp" src={data.fp4.childImageSharp.fluid.originalImg}alt="fp4.jpg"/>
                                        </div>
                                </PropertyItem>
                            </li>
                            <li className="extrawide list-inline-item">
                            <PropertyItem listfor='For Rent'
                                    price="13000"category="Apartment"
                                    title="Luxury Family Home"
                                    featured
                                    >
                                        <div className="item">
                                            <img className="img-whp" src={data.fp4.childImageSharp.fluid.originalImg} alt="fp4.jpg"/>
                                        </div>
                                        <div className="item">
                                            <img className="img-whp" src={data.fp5.childImageSharp.fluid.originalImg}alt="fp5.jpg"/>
                                        </div>
                            </PropertyItem>
                            </li>
                            <li className="extrawide list-inline-item">
                                <PropertyItem listfor='For Sale'
                                featured
                                price="13000"
                                category="Appartment"
                                title="Renovated Apartment"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp15.childImageSharp.fluid.originalImg} alt="fp15.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp16.childImageSharp.fluid.originalImg}alt="fp16.jpg"/>
                                    </div>
                                </PropertyItem>
                            </li>
                            <li className="extrawide list-inline-item">
                                <PropertyItem listfor='For Rent'
                                price="13000"category="Villa"
                                title="Gorgeous Villa Bay View"
                                >
                                    <div className="item">
                                        <img className="img-whp" src={data.fp16.childImageSharp.fluid.originalImg} alt="fp16.jpg"/>
                                    </div>
                                    <div className="item">
                                        <img className="img-whp" src={data.fp17.childImageSharp.fluid.originalImg}alt="fp17.jpg"/>
                                    </div>
                                </PropertyItem>
                           </li>
                        </ul>                       
                        </div>
                        <div className="col-lg-12 mb30">
                                <Pagination/>
                            </div> 
                     </div>
					</div>
				</div>
            </div>
        </div>
	</section>
    )
}

export default HalfMapV1Section
